export default {
	data() {
		return {
			title : '',
			content : ''
		}
	},
	onLoad:function(e){
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+ e.postid,
			method: 'GET',
			data: {},
			success: res => {
				// console.log(res.data.post_id);
				console.log(res);
				this.title = res.data.title;
				this.content = res.data.content;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
